.container-mobile {
  width: 90%;
  margin: 0 auto;
}

@media only screen and (max-width: 1180px) {
  figure {
    width: 50%;
    float: right;
    display: inline;
  }

  .portrait {
    width: 100%;
    height: auto;
  }
}

@media only screen and (max-width: 767px) {
  .more li a {
    font-size: 2rem;
  }

  section {
    margin-bottom: 5rem;
  }

  iframe {
    width: 100%;
  }

  figure {
    width: 100%;
    margin-left: 0;
    display: block;
  }

  .featured {
    margin: 0;
    padding: 0;
    bottom: 0;
    left: 5%;
    overflow-x: hidden;
  }

  #featured-span {
    font-size: 3.5rem;
  }

  footer {
    text-align: center;
    padding: 2rem 0;
    font-size: 1.3rem;
  }

  .legal-links {
    float: none;
    text-align: center;
  }

  .legal-links li {
    margin-left: 0;
    margin-right: 1.5rem;
  }

  .horn-list a {
    font-size: 1.8rem;
  }

  .horn-list li {
    margin-left: 2rem;
    font-size: 1.8rem;
    line-height: 2;
  }

  .dedicatee li, .dedicatee li a {
    font-size: 2rem;
  }

  .items {
    grid-template-columns: repeat(2, 1fr);
  }

  .instructions {
    font-size: 1.6rem;
  }

  .copyrights a {
    font-size: 2rem;
  }

  h1 {
    font-size: 3.5rem;
    line-height: 1.5;
  }

  .title {
    margin-bottom: 1rem;
    font-size: 2.5rem;
  }

  body {
    font-size: 2rem;
    line-height: 1.7;
  }

  .featured-text {
    font-size: 1.8rem;
    line-height: 1.5;
  }

  h2 {
    font-size: 2.5rem;
  }

  .featured h2 {
    margin-bottom: 1rem;
    font-size: 2rem;
  }

  .portrait {
    width: 100%;
    height: auto;
  }
}

@media screen and (max-width: 600px) {
  .topnav a:not(:first-child) {
    display: none;
  }

  .topnav a.icon {
    float: right;
    display: block;
  }

  .topnav.responsive {
    position: relative;
  }

  .topnav.responsive a.icon {
    position: absolute;
    top: 0;
    right: 5vw;
  }

  .topnav.responsive a {
    float: none;
    text-align: center;
    display: block;
  }
}

